.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.keyboard__wrapper {
  display: flex;
    flex-direction: column;
    align-items: center;
}
.row {
  display: flex;
}
.letter {
  padding: 5px;
  background-color: orange;
  border: 1px solid black;
  margin: 10px 5px;
  padding: 5px 15px;
}
body {
  background: url(../src/1.png);
  color: aliceblue;
  font-size: 36px;
}
.input {
  background-color: red;
  position: absolute;
  top: 49px;
  opacity: 50%;
  width: 96.2%;
  padding: 15px;
  height: 6%;
}
.main {
  color: #ffffffd6;
  background-color: #61dafb;
  position: relative;
  padding: 15px;
}
.popup {
  position: absolute;
  width: 50%;
  right: 30%;
  height: 50%;
  background: black;
}
.popup__wrapper {
  padding: 15px;
  text-align: center
}
.timer {
  text-align: center;
  background-color: gray;
  width: 50%;
  margin: 0 auto;
}
.active {
  background-color: rgba(0, 149, 255, 0.557);
  transition: 0.5s ease-in;
}
.mistake {
  position: absolute;
  background-color: black;
  width: 166px;
}